import Grid from "@mui/material/Grid"
import * as React from "react"
import { useCallback } from "react"
import { Texture } from "tsgl"
import { PanningCanvasRenderer } from "../noises"
import {
  GenericDemo,



} from "../sdfs/SimpleCanvasRenderer"
import openSansRegularPng from "./OpenSans-Regular.png"

function Demo2D({
  frag,
  sx,
  animate,
  state,
}





) {
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const Renderer = useCallback(
    class extends PanningCanvasRenderer {
      
      
      constructor(
        canvas,
        options,
      ) {
        // eslint-disable-next-line @typescript-eslint/no-var-requires
        super(() => require("./" + frag + ".frag"), canvas, options)

        this.tex = Texture.fromURLSwitch(openSansRegularPng, undefined, this.gl)
      }
    },
    [frag],
  )

  return (
    React.createElement(GenericDemo, {
      focusable: true,
      sx: sx,
      Renderer: Renderer,
      animate: true,
      state: state,}
    )
  )
}

export default () => {
  return (
    React.createElement(Grid, { container: true, spacing: 2, padding: 2,}
      , [
        "demoGear",
        "demoRectangle",
        "demoText",
        "demoNgon",
        "demoHexagon",
        "demoOctagon",
        "demoTriIso",
        "demoArrow",
        "demoHeart",
        "demoTriEqui",
        "demoSegment",
        "demoTri",
        "demoArc",
      ].map((frag) => (
        React.createElement(Grid, { key: frag, item: true, xs: 12, md: 6, lg: 4,}
          , React.createElement(Demo2D, { frag: frag, sx: { height: 300 }, animate: true, state: {},} )
        )
      ))
    )
  )
}
